	module.exports = {"data":{"challengeNode":{"title":"Problem 403: Lattice points enclosed by parabola and line","description":["For integers a and b, we define D(a, b) as the domain enclosed by the parabola y = x2 and the line y = a·x + b:D(a, b) = { (x, y) | x2 ≤ y ≤ a·x + b }.","","","L(a, b) is defined as the number of lattice points contained in D(a, b).","For example, L(1, 2) = 8 and L(2, -1) = 1.","","","We also define S(N) as the sum of L(a, b) for all the pairs (a, b) such that the area of D(a, b) is a rational number and |a|,|b| ≤ N.","We can verify that S(5) = 344 and S(100) = 26709528.","","","Find S(1012). Give your answer mod 108."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-403-lattice-points-enclosed-by-parabola-and-line","blockName":"Project Euler","tests":[{"text":"<code>euler403()</code> should return 18224771.","testString":"assert.strictEqual(euler403(), 18224771, '<code>euler403()</code> should return 18224771.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler403() {\n  // Good luck!\n  return true;\n}\n\neuler403();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-404-crisscross-ellipses","id":"5900f5001000cf542c510013"},"slug":"/coding-interview-prep/project-euler/problem-403-lattice-points-enclosed-by-parabola-and-line"}}

